import React from 'react';

const MainHeading = () => {
  return (
    <header className="mainHeading">
      <article className="mainHeading__text">
        <h1 className="mainHeading__title">chibaosinger</h1>
        <h2 className="mainHeading__subtitle">
          <span className="mainHeading__subtitleWord">creative</span>
          <span className="mainHeading__subtitleWord">developer</span>
        </h2>
        <div className="mainHeading__description">
          <div className="mainHeading__description--line1">
            <span className="mainHeading__description--strike">ui designer</span>
            <p>css enthusiast</p>
          </div>
          <div className="mainHeading__description--line2">
            <p>trying to make coding look cool.</p>
            <span className="mainHeading__description--super">super</span>
          </div>
        </div>
      </article>
      <span className="mainHeading__scroll"></span>
    </header>
  )
}

export default MainHeading;